<template><undefined>
<h1>Steps 步骤条</h1>
<p>引导用户按照流程完成任务的导航条。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>当任务复杂或者存在先后关系时，将其分解成一系列步骤，从而简化任务。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-steps :current=\&quot;1\&quot;&gt;\n    &lt;v-step title=\&quot;已完成\&quot; description=\&quot;这里是多信息的描述\&quot;&gt;&lt;/v-step&gt;\n    &lt;v-step title=\&quot;进行中\&quot; description=\&quot;这里是多信息的描述\&quot;&gt;&lt;/v-step&gt;\n    &lt;v-step title=\&quot;待运行\&quot; description=\&quot;这里是多信息的描述\&quot;&gt;&lt;/v-step&gt;\n    &lt;v-step title=\&quot;待运行\&quot; description=\&quot;这里是多信息的描述\&quot;&gt;&lt;/v-step&gt;\n&lt;/v-steps&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-steps :current="1">
    <v-step title="已完成" description="这里是多信息的描述"></v-step>
    <v-step title="进行中" description="这里是多信息的描述"></v-step>
    <v-step title="待运行" description="这里是多信息的描述"></v-step>
    <v-step title="待运行" description="这里是多信息的描述"></v-step>
</v-steps>
</div>
