<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-steps&gt;\n    &lt;v-step status=\&quot;finish\&quot; title=\&quot;步骤1\&quot; icon=\&quot;cloud\&quot;&gt;&lt;/v-step&gt;\n    &lt;v-step status=\&quot;process\&quot; title=\&quot;步骤2\&quot; icon=\&quot;apple\&quot;&gt;&lt;/v-step&gt;\n    &lt;v-step status=\&quot;wait\&quot; title=\&quot;步骤3\&quot; icon=\&quot;github\&quot;&gt;&lt;/v-step&gt;\n&lt;/v-steps&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
