    <span class="hljs-tag">&lt;<span class="hljs-name">v-step</span> <span class="hljs-attr">status</span>=<span class="hljs-string">"process"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"步骤2"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"apple"</span> &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-step</span>&gt;</span>
