<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-steps</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-step</span> <span class="hljs-attr">status</span>=<span class="hljs-string">"finish"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"步骤1"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"cloud"</span> &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-step</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-step</span> <span class="hljs-attr">status</span>=<span class="hljs-string">"process"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"步骤2"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"apple"</span> &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-step</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-step</span> <span class="hljs-attr">status</span>=<span class="hljs-string">"wait"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"步骤3"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"github"</span> &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-step</span>&gt;</span>
