</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div&gt;\n        &lt;div style=\&quot;margin-bottom: 24px\&quot;&gt;当前正在执行第 {{current}} 步&lt;/div&gt;\n        &lt;v-steps :current=\&quot;current - 1\&quot;&gt;\n          &lt;v-step :key=\&quot;s.index\&quot; :title=\&quot;s.title\&quot; v-for=\&quot;s in steps\&quot;&gt;&lt;/v-step&gt;\n        &lt;/v-steps&gt;\n        &lt;div style=\&quot;margin-top: 24px\&quot;&gt;\n          &lt;button type=\&quot;button\&quot; name=\&quot;button\&quot; class=\&quot;ant-btn\&quot; @click=\&quot;next\&quot;&gt;下一步&lt;/button&gt;\n        &lt;/div&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        return {\n            current: 1,\n            steps: [\n                {\n                    title: \&quot;步骤1\&quot;,\n                    index: 0\n                },\n                {\n                    title: \&quot;步骤2\&quot;,\n                    index: 1\n                },\n                {\n                    title: \&quot;步骤3\&quot;,\n                    index: 2\n                },\n                {\n                    title: \&quot;步骤4\&quot;,\n                    index: 3\n                }\n            ],\n        }\n    },\n    methods: {\n        next() {\n            if (this.current &lt; this.steps.length) {\n                this.current ++\n            } else {\n                this.current = 1\n            }\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <div>
        <div style="margin-bottom: 24px">当前正在执行第 {{current}} 步</div>
        <v-steps :current="current - 1">
          <v-step :key="s.index" :title="s.title" v-for="s in steps"></v-step>
        </v-steps>
        <div style="margin-top: 24px">
          <button type="button" name="button" class="ant-btn" @click="next">下一步</button>
        </div>
    </div>
</template>

</div>
                  <div slot="description"><h4 id="qie-huan-bu-zou"><a class="header-anchor" href="#qie-huan-bu-zou" aria-hidden="true">¶</a> 切换步骤</h4>
<p>通常配合内容及按钮使用，表示一个流程的处理进度。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-bottom: 24px"</span>&gt;</span>当前正在执行第 <span>{{</span>current<span>}}</span> 步<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-steps</span> <span class="hljs-attr">:current</span>=<span class="hljs-string">"current - 1"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">v-step</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"s.index"</span> <span class="hljs-attr">:title</span>=<span class="hljs-string">"s.title"</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"s in steps"</span> &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-step</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">v-steps</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-top: 24px"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ant-btn"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"next"</span>&gt;</span>下一步<span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">current</span>: <span class="hljs-number">1</span>,
            <span class="hljs-attr">steps</span>: [
                {
                    <span class="hljs-attr">title</span>: <span class="hljs-string">"步骤1"</span>,
                    <span class="hljs-attr">index</span>: <span class="hljs-number">0</span>
                },
                {
                    <span class="hljs-attr">title</span>: <span class="hljs-string">"步骤2"</span>,
                    <span class="hljs-attr">index</span>: <span class="hljs-number">1</span>
                },
                {
                    <span class="hljs-attr">title</span>: <span class="hljs-string">"步骤3"</span>,
                    <span class="hljs-attr">index</span>: <span class="hljs-number">2</span>
                },
                {
                    <span class="hljs-attr">title</span>: <span class="hljs-string">"步骤4"</span>,
                    <span class="hljs-attr">index</span>: <span class="hljs-number">3</span>
                }
            ],
        }
    },
    <span class="hljs-attr">methods</span>: {
        next() {
            <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.current &lt; <span class="hljs-keyword">this</span>.steps.length) {
                <span class="hljs-keyword">this</span>.current ++
            } <span class="hljs-keyword">else</span> {
                <span class="hljs-keyword">this</span>.current = <span class="hljs-number">1</span>
            }
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
