<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="steps-props"><a class="header-anchor" href="#steps-props" aria-hidden="true">¶</a> Steps Props</h3>
<p>整体步骤条。</p>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>current</td>
<td>指定当前步骤，从 0 开始记数。在子 Step 元素中，可以通过 status 属性覆盖状态</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>status</td>
<td>指定当前步骤的状态，可选 wait process finish error</td>
<td>String</td>
<td>process</td>
</tr>
<tr>
<td>size</td>
<td>指定大小，目前支持普通（default）和迷你（small）</td>
<td>String</td>
<td>default</td>
</tr>
<tr>
<td>direction</td>
<td>指定步骤条方向。目前支持水平和竖直（vertical）两种方向，默认水平方向）</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="steps-step-props"><a class="header-anchor" href="#steps-step-props" aria-hidden="true">¶</a> Steps.Step Props</h3>
<p>步骤条内的每一个步骤。</p>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>status</td>
<td>指定状态。当不配置该属性时，会使用 Steps 的 current 来自动指定状态。可选：wait process finish error</td>
<td>String</td>
<td>wait</td>
</tr>
<tr>
<td>title</td>
<td>标题</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>description</td>
<td>步骤的详情描述，可选</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>icon</td>
<td>步骤图标，可选</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
export default {
    data() {
        return {
            current: 1,
            steps: [
                {
                    title: "步骤1",
                    index: 0
                },
                {
                    title: "步骤2",
                    index: 1
                },
                {
                    title: "步骤3",
                    index: 2
                },
                {
                    title: "步骤4",
                    index: 3
                }
            ],
        }
    },
    methods: {
        next() {
            if (this.current < this.steps.length) {
                this.current ++
            } else {
                this.current = 1
            }
        }
    }
}
</script>


